<template>
    <div style="width: 600px; margin: auto">
        <h1 @click="openFreezer">KPI</h1>

        <h4 @click="openFreezer">Opens in New Tab Window</h4>
    </div>
</template>

<script>
import { onMounted, ref } from "vue-demi";
import { useStore } from "vuex";
import { NL_FORM_BASE } from "@/constants";

export default {
    setup() {
        const store = useStore();
        const openFreezer = () => {
            window.open(
                `${NL_FORM_BASE}/kpi/#/kpi`,
                "_blank" // <- This is what makes it open in a new window.
            );
        };

        onMounted(() => {
            openFreezer();
        });

        return {
            openFreezer
        };
    }
};
</script>
